import { useState } from "react";
import { v4 as uuidv4 } from 'uuid';
const MultipleInputArea = (props) => {
    const [inputs, setInputs] = useState([{id: uuidv4()}]);
    const hasDelete = inputs.length > 1 ? true : false;
    const handleAddNewInput = () => {
        let nInputs = [...inputs];
        nInputs.push({id: uuidv4()});
        setInputs(nInputs);
    }
    const handleDeleteInput = (id) => {
        if(inputs.length === 1) return;
        let nInputs = [...inputs];
        const elId = nInputs.findIndex(x=>x.id === id);
        nInputs.splice(elId, 1);
        setInputs(nInputs);
    }
    return (<>
        {
            inputs.map( (x,i) => <div key={x.id} className="input-group input-group-sm mb-2">
                <div className="input-group-prepend">
                    <span className="input-group-text">{props.name}</span>
                </div>
                <div>
                    {
                        props.hasSelect ? <>
                            <input
                                hidden
                                id={`${props.fieldPrefix}_${i+1}_ID`}
                                name={`${props.fieldPrefix}_${i+1}_ID`}
                            />
                            <input name={`${props.fieldPrefix}_${i+1}_SavedName`} hidden/>
                        </> : <></>
                    }
                    <input type="text" id={props.fieldPrefix + "_Name"} name={props.fieldPrefix + "_Name"} className="column_filter form-control" aria-label="Small" aria-describedby="inputGroup-sizing-sm"/>
                </div>
                <div className="input-group-append">
                    {
                        props.hasSelect ? <><button className="btn btn-secondary"
                        onClick={()=> props.onSelect(`${props.fieldPrefix}_${i+1}`)}><img alt="user select" src="https://ecm.socar.az/img/guy_select.gif"></img></button></> :<></>
                    }
                    <button className="btn btn-secondary" onClick={handleAddNewInput}><i className="bi bi-plus"></i></button>
                    {
                        hasDelete ? <button className="btn btn-secondary" onClick={() => handleDeleteInput(x.id)}><i className="bi bi-dash"></i></button>
                                        : <></>
                    }
                </div>     
            </div>)
        }
    </>)
}

export default MultipleInputArea;