import autocomplete from 'autocompleter';
import { useEffect } from 'react';

const Autocomplete = (props) => {
    const source = props.source.map(x => {
        x = { label: x.NAME, value: x.ID }
        return x;
    })
    useEffect(() => {
        var input = document.getElementById(props.id);
    
        autocomplete({
            input: input,
            minLength: 1,
            emptyMsg: 'Məlumat yoxdur 🚫',
            fetch: function(text, update) {
                text = text.toLowerCase();
                var suggestions = source.filter(n => n.label.toLowerCase().startsWith(text));
                update(suggestions);
            },
            onSelect: function(item) {
                input.value = item.label;
            }
        });
    })
    return (
        <div className="input-group input-group-sm mb-2">
            <div className="input-group-prepend">
                <span className="input-group-text">{props.name}</span>
            </div>
            <div>
                <input 
                    id={props.id} 
                    type="text" 
                    className="column_filter form-control" 
                    aria-label="Small" 
                    aria-describedby="inputGroup-sizing-sm" 
                    autocomplete="off"
                    onChange={props.change} 
                />
            </div> 
            { props.children }
        </div>
    )
}

export default Autocomplete;