import create from "zustand";
import { devtools } from 'zustand/middleware';
import {v4 as uuid} from 'uuid';
import moment from "moment";
import axios from "axios";

const handleSelectYear = (set, get, selectedYear) => {
    if(selectedYear === 'none') 
    {
        set({selectedYear, minDate: null, maxDate: null});
        return;
    }
    let currentYear = get().defaultYear;
    let minDate = new Date(selectedYear);
    let maxDate = null;
    if(currentYear !== selectedYear) maxDate = new Date(new Date(selectedYear).getFullYear(), 11, 31);
    else maxDate = new Date();
    set({selectedYear, minDate, maxDate, fromDate: minDate, toDate: maxDate});
}

const handleGetSummaryList = async (set, get) => {
    const result = await axios.get('https://ecm.socar.az/otcs/llisapi.dll/api/v1/nodes/180578708/output?format=json', {
      headers: {
          'OTCSTicket': window.OTCSTicket
      }
    });
    const data = JSON.parse(result.data.data).data;
    data.pop();
    set({summaryList: data});
}

const handleGetCompanyList = async (set, get) => {
    const result = await axios.get('https://ecm.socar.az/otcs/llisapi.dll/api/v1/nodes/180575362/output?format=json', {
        headers: {
            'OTCSTicket': window.OTCSTicket
        }
    });
    const data = JSON.parse(result.data.data).data;
    data.pop();
    set({companyList: data});
}

const handleGetOTCSTicket = async (set, get) => {
    const formdata = new FormData();
    formdata.append("username", "vugar.i.eyvazov");
    formdata.append("password", "vugar123");
    const requestOptions = {
        method: 'POST',
        body: formdata,
        redirect: 'follow'
    }
    const result = await fetch("https://ecm.socar.az/otcs/llisapi.dll/api/v1/auth", requestOptions);
    const ticket = (await result.json()).ticket;
    window.OTCSTicket = ticket;
}

