import { useState } from "react";
import getDocuments from "../../../hooks/getDocuments";
import useOutgoing from "../../../store/useOutgoing";
import shallow from 'zustand/shallow';
import OutgoingComponents from "./OutgoingComponents";
import { userSelect } from '../../../lib/opentext/userSelect';

const getState = state => [
    state.years, state.types,
    state.defaultYear, state.defaultType,
    state.disapprovedStatuses, state.approvedStatuses,
    state.selectedYear, state.setSelectedYear,
    state.minDate, state.maxDate, state.fromDate, state.toDate,
    state.setFromDate, state.setToDate, state.summaryList, state.companyList
]
const Outgoing = () => {

    const [years, types, defaultYear, defaultType,
        disapprovedStatuses, approvedStatuses,
        selectedYear, setSelectedYear, minDate, maxDate,
        fromDate, toDate, setFromDate, setToDate, summaryList, companyList
    ] = useOutgoing(getState, shallow);

    const [documents, setDocuments] = useState([]);
    const [wait, setWait] = useState(false);
    const [statusList, setStatusList] = useState(approvedStatuses); 
    const handleGetResult = async () => {
        setWait(true);
        const tmp = (await getDocuments()).data;
        const docs = JSON.parse(tmp).data;
        docs.pop();
        setDocuments(docs);
        setWait(false);
    }
    const handleChangeInitiator = (event) => {
         window.gridTable.columns(6).search(event.target.value).draw();   
    }
    const handleChangeSenderOrganization = (event) => {
        window.gridTable.columns(0).search(event.target.value).draw();   
    } 
    const handleSelectTypeChange = (event) => {
        const selectedType = event.target.value;
        let statuses = [];
        if(selectedType === 'Təsdiqlənmiş'){
            statuses = [...approvedStatuses];
        }else if(selectedType === 'Təsdiqlənməmiş'){
            statuses = [...disapprovedStatuses];
        }
        setStatusList(statuses);
    }
    const handleStatusChange = (event) => {
        console.log(console.log(event.target.value));
    }

    const handleFromDateChanged = (date) => {
        setFromDate(date);
        //window.gridTable.draw();
    }

    const handleToDateChanged = (date) => {
        setToDate(date);
        //window.gridTable.draw();
    }

    const handleYearChange = (event) => {
        setSelectedYear(event.target.value);
    }   
    const summary = summaryList.map(x => {
        x = { id: x.SNO, value: x.SUMMARY, name: x.SUMMARY };
        return x;
    })
    const handleInitiatorSelect = fieldPrefix => {
        window.portal = userSelect('outgoing', fieldPrefix);
        var timer = setInterval(async function () {
            try {
                if (window.portal.closed) {
                    clearInterval(timer);
                    console.log(document.getElementById(fieldPrefix + '_ID').value);
                }
            } catch (e) {
            }
        }, 1000);
    }
    const handleSubmit = (event) => {
        event.preventDefault();
    }
    const handleSenderSelect = (fieldPrefix) => {
        window.portal = userSelect('outgoing', fieldPrefix);
        var timer = setInterval(async function () {
            try {
                if (window.portal.closed) {
                    clearInterval(timer);
                    console.log(document.getElementById(fieldPrefix + '_ID').value);
                }
            } catch (e) {
            }
        }, 1000);
    }
    const args = {
        years, defaultType, defaultYear, types,
        wait, documents, statusList, 
        minDate, maxDate, fromDate, toDate,
        summary, companyList,
        fieldPrefix: {
            initiator: 'initiator',
            sender: 'sender'
        },
        onStatusChange: handleStatusChange,
        onGetResult: handleGetResult,
        onChangeSenderOrganization: handleChangeSenderOrganization ,
        onChangeInitiator: handleChangeInitiator,
        onSelectTypeChanged: handleSelectTypeChange,
        onFromDateChanged: handleFromDateChanged,
        onToDateChanged: handleToDateChanged,
        onYearChange: handleYearChange,
        onInitiatorSelect: handleInitiatorSelect,
        onSubmit: handleSubmit,
        onSenderSelected: handleSenderSelect      
    }
    return (
       <OutgoingComponents { ...args } />
    );
}

export default Outgoing;