import { useState } from "react";
import { v4 as uuidv4 } from 'uuid';
const MultipleSelectArea = (props) => {
    const [inputs, setInputs] = useState([{id: uuidv4()}]);
    const hasDelete = inputs.length > 1 ? true : false;
    const handleAddNewInput = () => {
        let nInputs = [...inputs];
        nInputs.push({id: uuidv4()});
        setInputs(nInputs);
    }
    const handleDeleteInput = (id) => {
        if(inputs.length === 1) return;
        let nInputs = [...inputs];
        const elId = nInputs.findIndex(x=>x.id === id);
        nInputs.splice(elId, 1);
        setInputs(nInputs);
    }
    return (<>
        {
            inputs.map( x => <div key={x.id} className="input-group input-group-sm mb-2">
                <div className="input-group-prepend">
                    <span className="input-group-text">{props.name}</span>
                </div>
                <div>
                    <select className="column_filter form-control" 
                            aria-label="Small" 
                            aria-describedby="inputGroup-sizing-sm"
                            onChange={props.onSelectChanged}
                            defaultValue="none"
                    >
                       <option value="none" disabled>Seç</option>
                        {
                            props.options && props.options.map(x => <option key={x.id} value={x.value}>{x.name}</option>)
                        }
                    </select>
                </div>
                <div className="input-group-append">
                    <button className="btn btn-secondary" onClick={handleAddNewInput}><i className="bi bi-plus"></i></button>
                    {
                        hasDelete ? <><button className="btn btn-light" onClick={() => handleDeleteInput(x.id)}><i className="bi bi-dash"></i></button></>
                                        : <></>
                    }
                </div>     
            </div>
            )
        }</>)
}

export default MultipleSelectArea;