import MyDatePicker from "../../../components/MyDatePicker";
import InputArea from "../../../components/InputArea";
import SelectArea from '../../../components/SelectArea';
import Datatable from "../Components/Datatable";
import Autocomplete from "../../../components/Autocomplete";
import MultipleInputArea from "../../../components/MultipleInputArea";
import MultipleSelectArea from "../../../components/MultipleSelectArea";

const OutgoingComponents = (props) => {
    return (
        <form name="outgoing" onSubmit={props.onSubmit}>
            <div className="container-fluid">
                <div className="row">
                    <div className="col-5">
                        <Autocomplete id="from_whom" name="Hansı idarədən" source={props.companyList} />
                        <MultipleInputArea name="Hansı idarəyə" />
                    </div>
                    <div className="col-5">
                        <input
                            hidden
                            id={props.fieldPrefix.initiator + "_ID"}
                            name={props.fieldPrefix.initiator + "_ID"}
                        />
                        <input name={props.fieldPrefix.initiator + "_SavedName"} hidden/>
                        <InputArea id={props.fieldPrefix.initiator + "_Name"} name={props.fieldPrefix.initiator + "_Name"} label="Təşəbbüskar" change={props.onChangeInitiator}>
                            <div className="input-group-append">
                                <button className="btn btn-secondary" onClick={()=> props.onInitiatorSelect(props.fieldPrefix.initiator)}><img alt="user select" src="https://ecm.socar.az/img/guy_select.gif"></img></button>
                            </div>
                        </InputArea>                      
                        <MultipleInputArea 
                            name="Göndərən"
                            hasSelect={true}
                            fieldPrefix={`${props.fieldPrefix.sender}`}
                            onSelect = {props.onSenderSelected}
                        />                        
                    </div>
                    <div className="col-2"></div>
                </div>
                <hr></hr>
                <div className="row">
                    <div className="col-5">
                        <SelectArea name="İl" defaultValue = {props.defaultYear} options={props.years} isNoneSelectDisabled = { false } onSelectChanged={props.onYearChange} />
                        <MyDatePicker id="outgoingFromDate" minDate={props.minDate} maxDate={props.maxDate} value={props.fromDate} name={"Hansı tarixdən"} onChange={props.onFromDateChanged} />
                        <MyDatePicker id="outgoingToDate" minDate={props.minDate} maxDate={props.maxDate} value={props.toDate}  name={"Hansı tarixə"} onChange={props.onToDateChanged} />
                    </div>
                    <div className="col-5">
                        <SelectArea name="Qısa məzmun" options={props.summary} isNoneSelectDisabled = { false } />
                        <SelectArea name="Tip" isNoneSelectDisabled = { true } defaultValue = {props.defaultType} options={props.types} onSelectChanged={props.onSelectTypeChanged} />             
                        <MultipleSelectArea 
                            name = "Status"
                            options = {props.statusList}
                            onSelectChanged = { props.onStatusChange }
                        />
                    </div>
                    <div className="col-2">
                        <button className="btn btn-secondary pull-bottom" onClick={props.onGetResult}>Nəticə</button>
                    </div>
                </div>
                <div className="app row mt-3">
                    {
                        props.wait ? 'Loading....' : <></>
                    }
                    <Datatable data = { props.documents }></Datatable>
                </div>
            </div>
        </form>
    );
}

export default OutgoingComponents;