import axios from 'axios';

const getDocuments = async () => {
  const result = await axios.get("https://ecm.socar.az/otcs/llisapi.dll/api/v1/nodes/180383760/output?format=json&fromDate=01.01.2022 00:00:00&toDate=31.12.2022 23:59:59", {
      headers: {
          'OTCSTicket': window.OTCSTicket
      }
  });
  return result.data;
};

export default getDocuments;
