const InputArea = (props) => {
    return (
        <div className="input-group input-group-sm mb-2">
            <div className="input-group-prepend">
                <span className="input-group-text">{props.label}</span>
            </div>
            <div>
                <input type="text" id={props.id} name={props.name} className="column_filter form-control" aria-label="Small" aria-describedby="inputGroup-sizing-sm" onChange={props.change} />
            </div> 
            {
                props.children
            }
        </div>
    )
}

export default InputArea;