import MainComponent from "./MainComponent";
import useOutgoing from "../store/useOutgoing";
import shallow from 'zustand/shallow';
import { useEffect } from "react";

const getState = state => [
    state.getSummaryList, 
    state.getCompanyList,
    state.getOTCSTicket];

const Main = () => {
    const [getSummaryList, getCompanyList, getOTCSTicket] = useOutgoing(getState, shallow);
    useEffect (() => {
        const initialLoader = async () => {
            await getOTCSTicket();
            await getSummaryList();
            await getCompanyList();
        } 
        initialLoader();
    })
    return <MainComponent />
}

export default Main;